import Vue from 'vue';
import VueRouter from 'vue-router';
import Home from '../views/Home.vue';

Vue.use(VueRouter);

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home,
  },
  {
    path: '/example',
    name: 'Example',
    component: () => import(/* webpackChunkName: "example" */ '../views/Example.vue'),
  },
  {
    path: '/about',
    name: 'About',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "about" */ '../views/About.vue'),
  },
  {
    path: '/old-home',
    name: 'Old Home',
    component: () => import(/* webpackChunkName: "old home" */ '../views/OldHome.vue'),
  },
  {
    path: '/topics',
    name: 'Topics',
    component: () => import(/* webpackChunkName: "topics" */ '../views/Topics.vue'),
  },
  {
    path: '/information',
    name: 'Information',
    component: () => import(/* webpackChunkName: "topics" */ '../views/Information.vue'),
  },
  {
    path: '/business',
    name: 'Business',
    component: () => import(/* webpackChunkName: "topics" */ '../views/Business.vue'),
  },
  {
    path: '/corporate-identity',
    name: 'CorporateIdentity',
    component: () => import(/* webpackChunkName: "topics" */ '../views/CorporateIdentity.vue'),
  },
  {
    path: '/liqlid',
    name: 'Liqlid',
    component: () => import(/* webpackChunkName: "topics" */ '../views/Liqlid.vue'),
  }
];

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes,
});

export default router;
